/* ColorPicker.css */
.color-picker-container {
    max-width: 600px;
    margin: 0 auto;
    padding: 20px;
    text-align: center;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    background-color: #f7f7f7;
    border-radius: 10px;
  }
  
  .color-options {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 10px;
  }
  
  .color-option {
    width: 80px;
    height: 80px;
    border-radius: 50%;
    cursor: pointer;
    transition: transform 0.2s ease-in-out;
  }
  
  .color-option:hover {
    transform: scale(1.1);
  }
  
  .color-option.selected {
    border: 3px solid #007bff;
    transform: scale(1.1);
  }
  
  .color-display {
    width: 100px;
    height: 100px;
    margin: 20px auto;
    border-radius: 10px;
  }
  
  p {
    font-size: 24px;
    margin-top: 10px;
  }